/* global Office, PowerPoint */

(() => {
    "use strict";

    // =========================
    // Config / Estado
    // =========================
    const NOTESET_SHARP = ["C", "C#", "D", "D#", "E", "F", "F#", "G", "G#", "A", "A#", "B"];
    const NOTESET_FLAT = ["C", "Db", "D", "Eb", "E", "F", "Gb", "G", "Ab", "A", "Bb", "B"];

    const NOTE_TO_INDEX = new Map([
        ["C", 0], ["B#", 0],
        ["C#", 1], ["Db", 1],
        ["D", 2],
        ["D#", 3], ["Eb", 3],
        ["E", 4], ["Fb", 4],
        ["F", 5], ["E#", 5],
        ["F#", 6], ["Gb", 6],
        ["G", 7],
        ["G#", 8], ["Ab", 8],
        ["A", 9],
        ["A#", 10], ["Bb", 10],
        ["B", 11], ["Cb", 11],
    ]);

    const STATE = {
        preferFlats: false, // false = sustenido (#), true = bemol (b)
    };

    // =========================
    // Regex de acorde SEM lookbehind
    // =========================
    // Estrat√©gia:
    //  - Captura o caractere anterior (ou come√ßo de linha) em (^|[^A-Za-z√Ä-√ø0-9])
    //  - Captura o acorde em si
    //  - Captura o caractere seguinte (ou fim de linha) em ($|[^A-Za-z√Ä-√ø0-9])
    //
    // Assim evitamos mexer em palavras:
    // - "ABRAM" n√£o casa porque depois do "A" vem letra ("B") => falha no "seguinte"
    // - "Eu" n√£o casa porque depois do "E" vem letra ("u") => falha
    //
    // Acorde permitido:
    //  (opcional "(") + raiz (A-G + #/b opcional) + sufixos comuns + (opcional /baixo) + (opcional ")")
    const CHORD_TOKEN_REGEX = /(^|[^A-Za-z√Ä-√ø0-9])(\(?)([A-G])([#b]?)(?:(?:maj|min|dim|aug|sus|add)?(?:m|M)?(?:\d+)?(?:M)?(?:\(\d+\))*)?(?:\/([A-G])([#b]?))?(\)?)(?=($|[^A-Za-z√Ä-√ø0-9]))/g;

    const CHORD_LABEL_REGEX = /\b(Intro|Final|Refr[a√£]o|Solo|Ponte|Verso|Coro|Tom)\b/i;

    // =========================
    // UI helpers
    // =========================
    function $(id) { return document.getElementById(id); }

    function setStatus(msg) {
        const el = $("status");
        if (el) el.textContent = msg || "";
    }

    function updateStateFromUI() {
        const flat = $("modeFlat");
        STATE.preferFlats = !!(flat && flat.checked);
    }

    // =========================
    // Transposi√ß√£o
    // =========================
    function normalizeNote(letter, accidental) {
        const name = `${letter}${accidental || ""}`;
        return NOTE_TO_INDEX.has(name) ? name : null;
    }

    function transposeNote(noteName, semitones, preferFlats) {
        const idx = NOTE_TO_INDEX.get(noteName);
        if (idx === undefined) return noteName;
        const newIdx = (idx + semitones + 12) % 12;
        return (preferFlats ? NOTESET_FLAT : NOTESET_SHARP)[newIdx];
    }

    function transposeChordParts(rootLetter, rootAcc, suffix, bassLetter, bassAcc, semitones, preferFlats) {
        const rootNorm = normalizeNote(rootLetter, rootAcc);
        if (!rootNorm) return null;

        const newRoot = transposeNote(rootNorm, semitones, preferFlats);

        let newBass = null;
        if (bassLetter) {
            const bassNorm = normalizeNote(bassLetter, bassAcc || "");
            if (bassNorm) newBass = transposeNote(bassNorm, semitones, preferFlats);
        }

        const left = `${newRoot}${suffix || ""}`;
        return newBass ? `${left}/${newBass}` : left;
    }

    // Decide se uma linha √© ‚Äúlinha de cifra‚Äù
    function isChordLine(line) {
        if (!line || !line.trim()) return false;

        let chordCount = 0;
        CHORD_TOKEN_REGEX.lastIndex = 0;

        let m;
        const matches = [];
        while ((m = CHORD_TOKEN_REGEX.exec(line)) !== null) {
            chordCount++;
            matches.push(m);
        }

        if (chordCount >= 2) return true;

        if (chordCount === 1) {
            const only = line.trim();
            const token = (matches[0][2] || "") + (matches[0][3] || "") + (matches[0][4] || "");
            // (n√£o confi√°vel sozinho, ent√£o fazemos outras checagens)
            if (CHORD_LABEL_REGEX.test(line)) return true;

            // Se ao tirar o acorde quase n√£o sobra letras, √© cifra
            const removed = line.replace(matches[0][0], " ");
            const leftoverLetters = (removed.match(/[A-Za-z√Ä-√ø]/g) || []).length;
            if (leftoverLetters <= 3) return true;

            // Se a linha √© s√≥ um token entre espa√ßos
            if (only.length <= (token.length + 4)) return true;
        }

        return false;
    }

    // Replacements em texto inteiro
    function computeReplacements(fullText, semitones, preferFlats) {
        const reps = [];
        if (!fullText) return reps;

        const lines = fullText.split(/\r\n|\n|\r/);
        let cursor = 0;

        for (const line of lines) {
            const lineStart = cursor;

            if (isChordLine(line)) {
                CHORD_TOKEN_REGEX.lastIndex = 0;
                let m;

                while ((m = CHORD_TOKEN_REGEX.exec(line)) !== null) {
                    // grupos:
                    // 1 prefix (come√ßo ou separador)
                    // 2 "(" opcional
                    // 3 root letter
                    // 4 root accidental
                    // depois vem sufixo dentro do mesmo match: a gente pega pelo slice entre root+acc e antes do /bass ou antes do ")"
                    // 5 bass letter (opcional)
                    // 6 bass accidental (opcional)
                    // 7 ")" opcional
                    // 8 lookahead (n√£o captura no array como grupo, mas usamos via (?=...))

                    const prefix = m[1] || "";
                    const openParen = m[2] || "";
                    const rootLetter = m[3] || "";
                    const rootAcc = m[4] || "";
                    const bassLetter = m[5] || null;
                    const bassAcc = m[6] || "";
                    const closeParen = m[7] || "";

                    // Para descobrir o sufixo, pegamos o trecho entre (root+acc) e ( /bass ou closeParen )
                    // m[0] inclui prefix + resto do acorde (at√© antes do lookahead)
                    // Vamos reconstruir o acorde ‚Äúcore‚Äù pela posi√ß√£o:
                    // texto do match sem prefix:
                    const matchText = m[0];
                    const withoutPrefix = matchText.slice(prefix.length); // come√ßa no "(" ou na letra
                    // remove openParen
                    const afterOpen = withoutPrefix.startsWith(openParen) ? withoutPrefix.slice(openParen.length) : withoutPrefix;
                    // agora come√ßa com rootLetter+rootAcc...
                    const afterRoot = afterOpen.slice((rootLetter + rootAcc).length);

                    // afterRoot cont√©m suffix + talvez "/bass" + closeParen
                    let suffix = afterRoot;
                    if (bassLetter) {
                        const slashPos = afterRoot.lastIndexOf("/");
                        if (slashPos >= 0) {
                            suffix = afterRoot.slice(0, slashPos);
                        }
                    }
                    if (closeParen && suffix.endsWith(closeParen)) {
                        suffix = suffix.slice(0, -closeParen.length);
                    }

                    const newCore = transposeChordParts(
                        rootLetter,
                        rootAcc,
                        suffix,
                        bassLetter,
                        bassAcc,
                        semitones,
                        preferFlats
                    );

                    if (!newCore) continue;

                    const originalCore = `${openParen}${rootLetter}${rootAcc}${suffix}${bassLetter ? `/${bassLetter}${bassAcc || ""}` : ""}${closeParen}`;
                    const newToken = `${prefix}${openParen}${newCore}${closeParen}`;

                    // √çndices dentro da linha:
                    // match come√ßa em m.index, e inclui prefix tamb√©m.
                    // Queremos substituir somente o acorde (sem mexer no prefix), para preservar espa√ßamentos:
                    const chordStartInMatch = prefix.length;
                    const chordStart = lineStart + m.index + chordStartInMatch;
                    const chordLen = originalCore.length;

                    const currentChordText = line.substr(m.index + chordStartInMatch, chordLen);
                    if (currentChordText !== originalCore) {
                        // seguran√ßa: se por algum motivo n√£o bater, pula
                        continue;
                    }

                    const newChordText = `${openParen}${newCore}${closeParen}`;
                    if (newChordText !== originalCore) {
                        reps.push({ start: chordStart, length: chordLen, newText: newChordText });
                    }
                }
            }

            cursor += line.length + 1; // assume \n
        }

        return reps;
    }

    // =========================
    // PowerPoint: aplicar no slide atual
    // =========================
    async function applyToCurrentSlide(semitones) {
        updateStateFromUI();

        const preferFlats = STATE.preferFlats;
        const modeLabel = preferFlats ? "bem√≥is (b)" : "sustenidos (#)";

        try {
            setStatus("Lendo o slide atual...");

            await PowerPoint.run(async (context) => {
                const slides = context.presentation.getSelectedSlides();
                slides.load("items");
                await context.sync();

                if (!slides.items || slides.items.length === 0) {
                    throw new Error("Nenhum slide selecionado. Clique na miniatura do slide e tente novamente.");
                }

                const slide = slides.items[0];
                const shapes = slide.shapes;
                shapes.load("items");
                await context.sync();

                let totalReplacements = 0;

                for (const shape of shapes.items) {
                    shape.load("textFrame");
                    await context.sync();

                    if (!shape.textFrame) continue;

                    shape.textFrame.load("hasText");
                    await context.sync();

                    if (!shape.textFrame.hasText) continue;

                    const tr = shape.textFrame.textRange;
                    tr.load("text");
                    await context.sync();

                    const text = tr.text || "";
                    const reps = computeReplacements(text, semitones, preferFlats);
                    if (reps.length === 0) continue;

                    reps.sort((a, b) => b.start - a.start);

                    for (const r of reps) {
                        const sub = tr.getSubstring(r.start, r.length);
                        sub.text = r.newText;
                        totalReplacements++;
                    }

                    await context.sync();
                }

                setStatus(`OK: slide atual | modo ${modeLabel} | altera√ß√µes: ${totalReplacements}`);
            });
        } catch (err) {
            console.error(err);
            setStatus(`Erro: ${err && err.message ? err.message : String(err)}`);
        }
    }

    function applyNotationOnly() {
        return applyToCurrentSlide(0);
    }

    // =========================
    // Boot
    // =========================
    Office.onReady(() => {
        const btnUp = $("btnUp");
        const btnDown = $("btnDown");
        const btnSharps = $("btnSharps");
        const btnFlats = $("btnFlats");

        if (btnUp) btnUp.onclick = () => applyToCurrentSlide(+1);
        if (btnDown) btnDown.onclick = () => applyToCurrentSlide(-1);

        if (btnSharps) {
            btnSharps.onclick = () => {
                const sharp = $("modeSharp");
                if (sharp) sharp.checked = true;
                applyNotationOnly();
            };
        }

        if (btnFlats) {
            btnFlats.onclick = () => {
                const flat = $("modeFlat");
                if (flat) flat.checked = true;
                applyNotationOnly();
            };
        }

        const modeSharp = $("modeSharp");
        const modeFlat = $("modeFlat");
        if (modeSharp) modeSharp.onchange = () => { updateStateFromUI(); setStatus("Modo: sustenidos (#)."); };
        if (modeFlat) modeFlat.onchange = () => { updateStateFromUI(); setStatus("Modo: bem√≥is (b)."); };

        setStatus("Pronto. Selecione o slide (miniatura) e use os bot√µes.");
    });
})();
